"use client"

import { useState } from "react"
import { User } from "@/types/dating"
import { motion, useAnimation } from "framer-motion"

interface SwipeCardProps {
  user: User
  onSwipe: (userId: string, direction: 'left' | 'right') => void
}

export default function SwipeCard({ user, onSwipe }: SwipeCardProps) {
  const [currentImageIndex, setCurrentImageIndex] = useState(0)
  const controls = useAnimation()

  const handleSwipe = (direction: 'left' | 'right') => {
    controls.start({
      x: direction === 'left' ? -500 : 500,
      opacity: 0,
      transition: { duration: 0.3 }
    }).then(() => {
      onSwipe(user.id, direction)
    })
  }

  const nextImage = () => {
    if (!user.photos || user.photos.length === 0) return
    setCurrentImageIndex((prev) => 
      prev === user.photos.length - 1 ? 0 : prev + 1
    )
  }

  const prevImage = () => {
    if (!user.photos || user.photos.length === 0) return
    setCurrentImageIndex((prev) => 
      prev === 0 ? user.photos.length - 1 : prev - 1
    )
  }

  if (!user || !user.photos || user.photos.length === 0) {
    return (
      <div className="bg-gray-900 rounded-2xl p-8 text-center">
        <p className="text-gray-400">No profile image available</p>
      </div>
    )
  }

  return (
    <motion.div
      animate={controls}
      drag="x"
      dragConstraints={{ left: -200, right: 200 }}
      onDragEnd={(e, { offset, velocity }) => {
        if (Math.abs(offset.x) > 100) {
          handleSwipe(offset.x > 0 ? 'right' : 'left')
        }
      }}
      className="relative w-full max-w-sm mx-auto"
    >
      <div className="bg-white rounded-3xl overflow-hidden shadow-2xl border border-pink-200">
        <div className="relative">
          <img
            src={user.photos[currentImageIndex]}
            alt={user.name}
            className="w-full h-96 object-cover"
            onError={(e) => {
              (e.target as HTMLImageElement).src = 'data:image/svg+xml;base64,'
            }}
          />
          
          {user.photos.length > 1 && (
            <div className="absolute bottom-4 left-1/2 transform -translate-x-1/2 flex space-x-2">
              {user.photos.map((_, index) => (
                <div
                  key={index}
                  className={`w-2 h-2 rounded-full ${
                    index === currentImageIndex ? 'bg-pink-500' : 'bg-pink-300'
                  }`}
                />
              ))}
            </div>
          )}

          {user.verified && (
            <div className="absolute top-4 right-4 bg-gradient-to-r from-pink-500 to-rose-500 text-white px-3 py-1 rounded-full text-xs font-bold flex items-center space-x-1">
              <span>‚úì</span>
              <span>VERIFIED</span>
            </div>
          )}
        </div>

        <div className="p-6">
          <div className="flex items-center justify-between mb-2">
            <h2 className="text-2xl font-bold text-pink-600">{user.name}, {user.age}</h2>
          </div>
          
          <p className="text-pink-700 mb-4">{user.bio}</p>
          
          <div className="flex flex-wrap gap-2 mb-4">
            {user.interests?.slice(0, 3).map((interest) => (
              <span
                key={interest}
                className="px-3 py-1 bg-gradient-to-r from-pink-100 to-rose-100 text-pink-700 rounded-full text-sm font-medium"
              >
                {interest}
              </span>
            ))}
          </div>
        </div>
      </div>

      <div className="flex justify-center space-x-8 mt-6">
        <motion.button
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.9 }}
          onClick={() => handleSwipe('left')}
          className="w-16 h-16 bg-gradient-to-r from-red-400 to-pink-400 rounded-full flex items-center justify-center text-white text-2xl shadow-lg hover:shadow-xl transition-all"
        >
          ‚úï
        </motion.button>
        
        <motion.button
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.9 }}
          onClick={() => handleSwipe('right')}
          className="w-16 h-16 bg-gradient-to-r from-green-400 to-emerald-400 rounded-full flex items-center justify-center text-white text-2xl shadow-lg hover:shadow-xl transition-all"
        >
          ‚ô•
        </motion.button>
      </div>
    </motion.div>
  )
}
