"use client"

import { User } from "@/types/dating"

interface MatchListProps {
  matches: User[]
}

export default function MatchList({ matches }: MatchListProps) {
  return (
    <div className="max-w-2xl mx-auto">
      <h1 className="text-2xl font-bold mb-6">Your Matches</h1>
      
      {matches.length === 0 ? (
        <div className="text-center text-gray-400 py-12">
          <p className="text-xl mb-2">No matches yet</p>
          <p>Keep swiping to find your perfect match!</p>
        </div>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          {matches.map((match) => (
            <div
              key={match.id}
              className="bg-gray-900 rounded-lg p-4 flex items-center space-x-4 hover:bg-gray-800 transition-colors cursor-pointer"
            >
              <img
                src={match.photos[0]}
                alt={match.name}
                className="w-16 h-16 rounded-full object-cover"
              />
              
              <div className="flex-1">
                <div className="flex items-center space-x-2">
                  <h3 className="font-semibold">{match.name}, {match.age}</h3>
                  {match.verified && (
                    <span className="text-blue-500 text-sm">‚úì</span>
                  )}
                </div>
                
                <p className="text-sm text-gray-400">{match.bio.substring(0, 50)}...</p>
                
                <div className="flex flex-wrap gap-1 mt-1">
                  {match.interests.slice(0, 2).map((interest) => (
                    <span
                      key={interest}
                      className="text-xs px-2 py-1 bg-gray-800 rounded"
                    >
                      {interest}
                    </span>
                  ))}
                </div>
              </div>
              
              <div className="text-right">
                <span className="text-xs text-gray-400">2h ago</span>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  )
}
