"use client"

import { useState, useEffect } from "react"
import { User, CrossPath } from "@/types/dating"

interface CrossPathDetectorProps {
  currentUser: User
  onCrossPath: (crossPath: CrossPath) => void
}

export default function CrossPathDetector({ currentUser, onCrossPath }: CrossPathDetectorProps) {
  const [nearbyUsers, setNearbyUsers] = useState<User[]>([])
  const [crossPaths, setCrossPaths] = useState<CrossPath[]>([])
  const [isAlarmActive, setIsAlarmActive] = useState(false)

  // Mock nearby users with common interests
  const mockNearbyUsers: User[] = [
    {
      id: "4",
      name: "Jessica",
      age: 27,
      bio: "Coffee enthusiast and book lover",
      photos: ["https://images.unsplash.com/photo-1544005313-94ddf0286df2?w=400"],
      interests: ["coffee", "photography", "travel"],
      verified: true,
      location: { lat: 40.7128, lng: -74.0060 }
    },
    {
      id: "5",
      name: "David",
      age: 30,
      bio: "Tech startup founder, love hiking and photography",
      photos: ["https://images.unsplash.com/photo-1472099645785-5658abf4ff4e?w=400"],
      interests: ["hiking", "photography", "technology"],
      verified: true,
      location: { lat: 40.7589, lng: -73.9851 }
    }
  ]

  useEffect(() => {
    // Simulate GPS location updates
    const interval = setInterval(() => {
      // Check for users within 100m radius
      const nearby = mockNearbyUsers.filter(user => {
        const distance = calculateDistance(
          currentUser.location.lat,
          currentUser.location.lng,
          user.location.lat,
          user.location.lng
        )
        return distance < 0.1 // 100 meters
      })

      setNearbyUsers(nearby)

      // Check for common interests
      nearby.forEach(user => {
        const commonInterests = currentUser.interests.filter(interest => 
          user.interests.includes(interest)
        )

        if (commonInterests.length > 0) {
          const distance = calculateDistance(
            currentUser.location.lat,
            currentUser.location.lng,
            user.location.lat,
            user.location.lng
          )

          const crossPath: CrossPath = {
            id: Date.now().toString(),
            user,
            location: user.location,
            timestamp: new Date(),
            commonInterests,
            distance
          }

          // Check if this cross-path is new
          const isNew = !crossPaths.some(cp => cp.user.id === user.id)
          if (isNew) {
            setCrossPaths(prev => [...prev, crossPath])
            onCrossPath(crossPath)
            setIsAlarmActive(true)
            
            // Play love alarm sound
            playLoveAlarm()
            
            // Auto-dismiss after 5 seconds
            setTimeout(() => {
              setIsAlarmActive(false)
            }, 5000)
          }
        }
      })
    }, 5000) // Check every 5 seconds

    return () => clearInterval(interval)
  }, [currentUser, crossPaths, onCrossPath])

  const calculateDistance = (lat1: number, lng1: number, lat2: number, lng2: number) => {
    const R = 6371 // Earth's radius in km
    const dLat = (lat2 - lat1) * Math.PI / 180
    const dLng = (lng2 - lng1) * Math.PI / 180
    const a = Math.sin(dLat/2) * Math.sin(dLat/2) +
              Math.cos(lat1 * Math.PI / 180) * Math.cos(lat2 * Math.PI / 180) *
              Math.sin(dLng/2) * Math.sin(dLng/2)
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a))
    return R * c
  }

  const playLoveAlarm = () => {
    // Create a simple beep sound using Web Audio API with error handling
    if (typeof window !== 'undefined' && (window as any).AudioContext) {
      try {
        const audioContext = new (window.AudioContext || (window as any).webkitAudioContext)()
        const oscillator = audioContext.createOscillator()
        const gainNode = audioContext.createGain()
        
        oscillator.connect(gainNode)
        gainNode.connect(audioContext.destination)
        
        oscillator.frequency.setValueAtTime(800, audioContext.currentTime)
        oscillator.type = 'sine'
        
        gainNode.gain.setValueAtTime(0.3, audioContext.currentTime)
        gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + 1)
        
        oscillator.start(audioContext.currentTime)
        oscillator.stop(audioContext.currentTime + 1)
      } catch (error) {
        console.warn('Audio playback failed:', error)
      }
    }
  }

  return (
    <div className="fixed bottom-4 right-4 z-50">
      {isAlarmActive && (
        <div className="bg-gradient-to-r from-pink-500 to-purple-500 text-white p-4 rounded-lg shadow-lg animate-pulse">
          <div className="flex items-center space-x-3">
            <div className="text-2xl">üíï</div>
            <div>
              <p className="font-bold">Love Alarm!</p>
              <p className="text-sm">Someone with common interests is nearby!</p>
            </div>
          </div>
        </div>
      )}

      <div className="mt-4 bg-gray-900 rounded-lg p-4 max-w-sm">
        <h3 className="font-semibold mb-2">Nearby Users</h3>
        {nearbyUsers.length === 0 ? (
          <p className="text-sm text-gray-400">No users nearby</p>
        ) : (
          <div className="space-y-2">
            {nearbyUsers.map((user) => {
              const commonInterests = currentUser.interests.filter(interest => 
                user.interests.includes(interest)
              )
              
              return (
                <div key={user.id} className="flex items-center space-x-3">
                  <img
                    src={user.photos[0]}
                    alt={user.name}
                    className="w-10 h-10 rounded-full object-cover"
                  />
                  <div>
                    <p className="text-sm font-medium">{user.name}, {user.age}</p>
                    {commonInterests.length > 0 && (
                      <p className="text-xs text-pink-400">
                        Common: {commonInterests.join(', ')}
                      </p>
                    )}
                  </div>
                </div>
              )
            })}
          </div>
        )}
      </div>
    </div>
  )
}
