"use client"

import { useState, useEffect } from "react"
import SwipeCard from "@/components/dating/SwipeCard"
import MatchList from "@/components/dating/MatchList"
import UserProfile from "@/components/dating/UserProfile"
import CrossPathDetector from "@/components/dating/CrossPathDetector"
import { User } from "@/types/dating"

export default function DatingApp() {
  const [currentUser, setCurrentUser] = useState<User>({
    id: "current",
    name: "Alex",
    age: 29,
    bio: "Adventure seeker and coffee lover. Looking for meaningful connections.",
    photos: ["https://images.unsplash.com/photo-1507003211169-0a1dd7228f2d?w=400"],
    interests: ["travel", "photography", "coffee", "hiking"],
    verified: true,
    location: { lat: 40.7128, lng: -74.0060 }
  })
  
  const [users, setUsers] = useState<User[]>([])
  const [matches, setMatches] = useState<User[]>([])
  const [activeTab, setActiveTab] = useState<'swipe' | 'matches' | 'profile'>('swipe')
  const [crossPaths, setCrossPaths] = useState<any[]>([])

  useEffect(() => {
    // Mock data for demo
    const mockUsers: User[] = [
      {
        id: "1",
        name: "Sarah",
        age: 28,
        bio: "Love hiking and coffee. Looking for genuine connections.",
        photos: ["https://images.unsplash.com/photo-1494790108755-2616b612b786?w=400"],
        interests: ["hiking", "coffee", "photography"],
        verified: true,
        location: { lat: 40.7128, lng: -74.0060 }
      },
      {
        id: "2",
        name: "Michael",
        age: 32,
        bio: "Tech enthusiast who loves cooking and traveling.",
        photos: ["https://images.unsplash.com/photo-1507003211169-0a1dd7228f2d?w=400"],
        interests: ["technology", "cooking", "travel"],
        verified: false,
        location: { lat: 40.7589, lng: -73.9851 }
      },
      {
        id: "3",
        name: "Emma",
        age: 26,
        bio: "Artist and yoga instructor. Passionate about wellness.",
        photos: ["https://images.unsplash.com/photo-1438761681033-6461ffad8d80?w=400"],
        interests: ["art", "yoga", "wellness"],
        verified: true,
        location: { lat: 40.7505, lng: -73.9934 }
      }
    ]
    setUsers(mockUsers)
  }, [])

  const handleSwipe = (userId: string, direction: 'left' | 'right') => {
    if (direction === 'right') {
      const matchedUser = users.find(u => u.id === userId)
      if (matchedUser) {
        setMatches(prev => [...prev, matchedUser])
      }
    }
    setUsers(prev => prev.filter(u => u.id !== userId))
  }

  const handleCrossPath = (crossPath: any) => {
    setCrossPaths(prev => [...prev, crossPath])
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-pink-50 via-purple-50 to-rose-50">
      {/* Love Connect Header */}
      <header className="bg-white/80 backdrop-blur-md shadow-lg border-b border-pink-200">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-2">
              <div className="w-8 h-8 bg-gradient-to-r from-pink-500 to-rose-500 rounded-full flex items-center justify-center">
                <span className="text-white font-bold text-sm">‚ù§Ô∏è</span>
              </div>
              <h1 className="text-2xl font-bold bg-gradient-to-r from-pink-600 to-rose-600 bg-clip-text text-transparent">
                Love Connect
              </h1>
            </div>
            <div className="flex space-x-1 bg-pink-100 rounded-full p-1">
              <button
                onClick={() => setActiveTab('swipe')}
                className={`px-4 py-2 rounded-full text-sm font-medium transition-all ${
                  activeTab === 'swipe' 
                    ? 'bg-gradient-to-r from-pink-500 to-rose-500 text-white shadow-lg' 
                    : 'text-pink-600 hover:bg-pink-200'
                }`}
              >
                Discover
              </button>
              <button
                onClick={() => setActiveTab('matches')}
                className={`px-4 py-2 rounded-full text-sm font-medium transition-all ${
                  activeTab === 'matches' 
                    ? 'bg-gradient-to-r from-pink-500 to-rose-500 text-white shadow-lg' 
                    : 'text-pink-600 hover:bg-pink-200'
                }`}
              >
                Matches {matches.length > 0 && `(${matches.length})`}
              </button>
              <button
                onClick={() => setActiveTab('profile')}
                className={`px-4 py-2 rounded-full text-sm font-medium transition-all ${
                  activeTab === 'profile' 
                    ? 'bg-gradient-to-r from-pink-500 to-rose-500 text-white shadow-lg' 
                    : 'text-pink-600 hover:bg-pink-200'
                }`}
              >
                Profile
              </button>
            </div>
          </div>
        </div>
      </header>

      <main className="container mx-auto p-4">
        {activeTab === 'swipe' && (
          <div className="flex flex-col items-center">
            <div className="text-center mb-6">
              <h2 className="text-3xl font-bold bg-gradient-to-r from-pink-600 to-rose-600 bg-clip-text text-transparent mb-2">
                Find Your Perfect Match
              </h2>
              <p className="text-pink-600">Connecting hearts together, one swipe at a time</p>
            </div>
            <div className="relative">
              {users.length > 0 ? (
                <SwipeCard user={users[0]} onSwipe={handleSwipe} />
              ) : (
                <div className="text-center bg-white/80 backdrop-blur-md rounded-2xl p-8 shadow-xl">
                  <div className="w-16 h-16 bg-gradient-to-r from-pink-500 to-rose-500 rounded-full flex items-center justify-center mx-auto mb-4">
                    <span className="text-white text-2xl">üíï</span>
                  </div>
                  <h3 className="text-xl font-semibold text-pink-600 mb-2">No more profiles nearby</h3>
                  <p className="text-pink-500">Expand your search or check back later!</p>
                </div>
              )}
            </div>
          </div>
        )}

        {activeTab === 'matches' && (
          <div>
            <div className="text-center mb-6">
              <h2 className="text-3xl font-bold bg-gradient-to-r from-pink-600 to-rose-600 bg-clip-text text-transparent mb-2">
                Your Matches
              </h2>
              <p className="text-pink-600">These people liked you back!</p>
            </div>
            <MatchList matches={matches} />
          </div>
        )}

        {activeTab === 'profile' && (
          <div>
            <div className="text-center mb-6">
              <h2 className="text-3xl font-bold bg-gradient-to-r from-pink-600 to-rose-600 bg-clip-text text-transparent mb-2">
                Your Profile
              </h2>
              <p className="text-pink-600">Make your profile shine!</p>
            </div>
            <UserProfile />
          </div>
        )}
      </main>

      <CrossPathDetector 
        currentUser={currentUser} 
        onCrossPath={handleCrossPath} 
      />
    </div>
  )
}
